nav {
    position: fixed !important;
    top: 0;
    z-index: 2;
    width: 100%;
}

body {
    padding-top: 60px;
}


.backBreadcrumb:hover {
    color: #7d8ead;
    cursor: pointer;
    text-decoration: underline;
}



.menuItem {
    height: 74px;
    margin-bottom: 6px;
    box-shadow: 0px 2px 4px #dfdfdf;
}

.menuItem:hover{
    background-color: #f9f9f9;
    cursor: pointer;
}

.menuItem .card-img {
    max-height: 72px;
    max-width: 72px;
    margin-right: auto;
    border-radius: 0;
    background-color: #515151;
}

.menuItem .card-img-overlay {
    padding: 8px 0 0 84px !important;
}

.menuItem .card-title {
    height: 32px;
    position: absolute;
    top: 6px;
    left: 84px;
    max-width: calc(100% - 130px);
}

.menuItem .card-title h5 {
    font-size: 18px;
    line-height: 18px;
}

.menuItem .card-subtitle {
    position: absolute;
    bottom: 6px;
    left: 84px;
}

.menuItem .card-text {
    position: absolute;
    padding: 6px 12px;
    margin: 0;
    bottom: 0;
    right: 0;
}

.menuItem .card-options {
    position: absolute;
    margin: 0;
    bottom: 0;
    right: 0;

}

.menuItem .card-options button {
    border-bottom-left-radius: 0;
    border-top-right-radius: 0;
    border: 0;
}

.menuItem .card-checkbox {
    height: 42px;
    width: 42px;
    position: absolute;
    top: 0;
    right: 0;
    border-bottom: 1px solid #dfdfdf;
    border-left: 1px solid #dfdfdf;
    border-bottom-left-radius: 4px;
    transition: background-color .2s;
}

.menuItem .card-checkbox:hover {
    background-color: #a5aaae;
}

.menuItem .card-checkbox span {
    padding-top: 13px;
    padding-right: 9px;
}

.menuItem .card-infoBox {
    height: 28px;
    width: 28px;
    position: absolute;
    top: 0;
    right: 0;
    border-bottom-left-radius: 4px;
    transition: background-color .2s;
    z-index: 5;
}

.menuItem .card-infoBox:hover {
    background-color: #ffffff;
}

.menuItem .card-infoBox span {
    padding-top: 7px;
    padding-right: 9px;
    color: #ddd !important;
}




.orderStatus{
    pointer-events: none;
    position: fixed;
    top: 56px;
    left: 0;
    width: 100%;
    z-index: 2;
}

.orderStatus .card{
    max-height: calc(100vh - 92px);
    overflow-y: auto;
}

.orderStatus .card{
    border-radius: 0;
}

.orderStatus .collapse {
    pointer-events: auto;
}

.orderStatusToggleButton button {
    position: relative;
    top: -2px;
    pointer-events: auto;
    border-bottom: 1px solid #dfdfdf;
    border-left: 1px solid #dfdfdf;
    border-radius: 0;
    border-bottom-left-radius: 8px;
    background-color: white;
}
@media (min-width: 992px) {
    .orderStatus{
        pointer-events: default;
        position: static;
        top: auto;
        left: auto;
        width: initial;
        z-index: default;
    }
    .orderStatus .card{
        max-height: calc(100vh - 66px);
        
    }
    .orderStatusToggleButton button {
        visibility: hidden;
    }
}

.test {
}

.test:hover{
    text-decoration: underline;
    cursor: pointer;
}

.test span {
    padding-left: 4px;
}

.test:focus {
    box-shadow: none !important;
}